(function (global, factory) {
  typeof exports === 'object' && typeof module !== 'undefined' ? module.exports = factory(require('redis-pubsub'), require('ainojs-ajax'), require('i18n-node')) :
  typeof define === 'function' && define.amd ? define(['redis-pubsub', 'ainojs-ajax', 'i18n-node'], factory) :
  global.MyLibrary = factory(global.Redispubsub, global.Ajax, global.i18n)
}(this, function (Redispubsub, Ajax, i18n) { 'use strict';

  class AjaxTranslationsLoader {
    constructor(options, sucessCallback, failCallback) {
      this.host = options.railsHost || '127.0.0.1';
      this.port = options.railsPort || 3000;
      this.restPath = options.restPath || AjaxTranslationsLoader.defaultPath;
      this.storage = options.storage;
      this.sucessCallback = sucessCallback;
      this.failCallback = failCallback;
      this.load();
    }

    static get defaultPath() {
      return '/translations';
    }

    get request() {
      return `'http://${this.host}:${this.port}/translations?${this.restPath}'`; // use String interpolation!
    }

    load() {
      Ajax.get(this.request).then(function(data) {
        console.log(data);
      }).catch(function(data) {
        console.log(data);
      });
    }
  }

  var ajaxTranslationsLoader = AjaxTranslationsLoader;

  class TranslationsLoader {
    constructor(options) {
      this.locales = options.locales;
      this.options = options;
    }

    load(locale, sucessCallback, failCallback) {
      this.options.restPath = `locale=${locale}`;
      return new ajaxTranslationsLoader(this.options, sucessCallback, failCallback);
    }
  }

  var translationsLoader = TranslationsLoader;

  class LocaleStorage {
    constructor(options) {
      this.loader = options.loader || LocaleStorage.defaultLoader;
      this.initialize();
    }

    static get defaultLoader() {
      return new ajaxTranslationsLoader(
        {},
        this.sucessCallback,
        this.failCalllback);
    }

    initialize() {
      // retrieve via loader
      for (let locale of this.loader.locales) {
        this.loader.load(
          locale,
          this.sucessCallback,
          this.failCalllback);
      }
    }

    sucessCallback(data) {
      // do stuff with data
      console.log('save');
      this.save(Object.keys(data)[0], JSON.stringify(data[Object.keys(data)[0]]));
    }

    failCalllback(data) {
      // do stuff with data
      console.log('fail');
      console.log(data);
    }

    save(key, value) {
      window.localStorage.setItem(key, value);
      console.log('localStorage = ' + key + ' : ' + value);
    }

    load(key) {
      window.localStorage.getItem(key);
    }
  }

  var localeStorage = LocaleStorage;

  /**
   * @class
   * An awesome script
   */
  class EventLogger {
    log(message) {
      console.log(message);
    }
  }

  var eventLogger = EventLogger;

  class Internationalizer {
    // constructor(options) {
    //   // ??
    // }

    init(options) {
      i18n.init(options);
    }
  }

  var internationalizer = Internationalizer;

  class StorageInternationalizer extends internationalizer {
    init(resource) {
      super.init(this.configurationFor(resource));
    }

    configurationFor(resource) {
      return {resStore: resource};
    }

  }

  var storageInternationalizer = StorageInternationalizer;

  /**
   * @class
   * An awesome script
   */
  class Translator {
    contructor(message) {
      this.key = Object.keys(message)[0];
      this.value = JSON.stringify(message[this.key]);
    }

    get locale() {
      return localStorage.getItem(this.key);
    }

    get translation() {
      return JSON.parse(this.locale);
    }

    // get resource() {
    //   return {key: {translation: value}};
    // }
  }

  var helpers_translator = Translator;

  class TranslationSynchronizer {
    constructor(options) {
      this.storage = options.storage;
    }

    sync(message) {
      var translator = new helpers_translator(message);
      (new storageInternationalizer()).init(translator.resource());
      this.storage.save(Object.keys(message)[0], JSON.stringify(message[Object.keys(message)[0]]));
    }
  }

  var translationSynchronizer = TranslationSynchronizer;

  class EventBus {
    constructor(options) {
      this.eventLogger = new eventLogger(options);
      this.translationSynchronizer = new translationSynchronizer(options);
      this.channel = Redispubsub.createChannel(
        options.realtimePort || 6379,
        options.realtimeHost || '127.0.0.1',
        options.channel || 'realtime_msg');
      this.init();
    }

    init() {
      var me = this;
      this.channel.on('connect', function() {
        me.channel.on('message', function(msg) {
          me.eventLogger.log(msg.msg);
          me.translationSynchronizer.sync(msg);
        });
      });
    }
  }

  var eventBus = EventBus;

  class TranslationManager {
    constructor(options) {
      this.options = options;
      options.loader = new translationsLoader(options || {});
      this.storage = new localeStorage(options);
      this.logger = options.logger || console.log;
      this.eventBus = new eventBus(options);
    }
  }

  var index = TranslationManager;

  return index;

}));
